






























import Vue from "vue";
export default Vue.component("Alert", {
  props: {
    msg: String,
  },
  data() {
    return {
      dismissSecs: 8,
      dismissCountDown: 0,
      showDismissibleAlert: false,
    };
  },
  methods: {
    countDownChanged(dismissCountDown: number) {
      this.dismissCountDown = dismissCountDown;
    },
    showAlert() {
      this.dismissCountDown = this.dismissSecs;
    },
  },
});
